import React, { useState } from 'react'
import './navbar.css'
import {
    Link
} from "react-router-dom";
const Navbar = () => {
    const [homeNavbar, setHomeNavbar] = useState({ NavHome: 'nav-list-home', NavDefault: '', HomeBtn: 'navbar_home_btn', DefaultBtn: '', text: 'Get Started' })
    const changeNavbarAccordingHome = () => {
        setHomeNavbar({ NavHome: 'nav-list-home', NavDefault: '', HomeBtn: 'navbar_home_btn', DefaultBtn: '', text: 'Get Started' });
    }
    const changeNavbarDefault = () => {
        setHomeNavbar({ NavHome: '', NavDefault: 'nav-list', HomeBtn: '', DefaultBtn: 'navbar_sign', text: 'Login' });
    }
    return (
        <nav className='navbar'>
            <ul className={`${homeNavbar.NavDefault} ${homeNavbar.NavHome}`}>
                <div className='logo'><img src="./favicon.png" alt='logo'></img></div>
                <li> <Link to='/' onClick={changeNavbarAccordingHome}>Home</Link></li>
                <li> <Link to='/' onClick={changeNavbarAccordingHome} >About</Link></li>
                <li> <Link to='/' onClick={changeNavbarAccordingHome} >Contactus</Link></li>
                <li> <Link to='/' onClick={changeNavbarAccordingHome} >Pricing</Link></li>
                <li> <Link to='/' onClick={changeNavbarAccordingHome} >Faqs</Link></li>
                <li> <Link to='/' onClick={changeNavbarAccordingHome} >Features</Link></li>
            </ul>
            <button className={`${homeNavbar.HomeBtn} ${homeNavbar.DefaultBtn}`} onClick={changeNavbarDefault} ><Link to="/login" >{homeNavbar.text}</Link></button>
        </nav>
    )
}

export default Navbar